import { useSearchParams } from "react-router-dom";
import { useAuth } from "../hooks/useAuth";
import { useForm } from "react-hook-form";
import { useState, useEffect } from "react";
import styles from "./Recover.module.css";
import { useNavigate } from "react-router-dom";

export default function Recover() {
  const navigate = useNavigate();
  const [showPassword, setShowPassword] = useState(false);
  const [showPassword2, setShowPassword2] = useState(false);
  const { register, setError, handleSubmit, formState, watch } = useForm({
    mode: "onSubmit",
    reValidateMode: "onSubmit",
  });
  const { errors } = formState;
  const { sendRecover } = useAuth();
  const [searchParams] = useSearchParams();
  const tokenFromUrl = searchParams.get("token");
  const passwordValue = watch("password");
  const passwordRHFError = errors.password?.message;
  const confirmRHFError = errors.confirm?.message;
  const serverRHFError = errors.root?.server?.message;
  const hasPasswordError = Boolean(passwordRHFError);
  const hasConfirmError = Boolean(confirmRHFError);
  const hasServerError = Boolean(serverRHFError);
  const [passwordErrorText, setPasswordErrorText] = useState("");
  const [confirmErrorText, setConfirmErrorText] = useState("");
  const [serverErrorText, setServerErrorText] = useState("");

  const onSubmit = async ({ password }) => {
    const res = await sendRecover({ token: tokenFromUrl, password });
    if (!res.ok) {
      setError("root.server", {
        type: "server",
        message: "Непредвиденная ошибка! Пожалуйста, попробуйте еще раз",
      });
    }
  };

  useEffect(() => {
    if (passwordRHFError) {
      setPasswordErrorText(passwordRHFError);
    }
  }, [passwordRHFError]);

  useEffect(() => {
    if (confirmRHFError) {
      setConfirmErrorText(confirmRHFError);
    }
  }, [confirmRHFError]);

  useEffect(() => {
    if (serverRHFError) {
      setServerErrorText(serverRHFError);
    }
  }, [serverRHFError]);

  return (
    <div className={styles.container}>
      <div className={styles["wrapper_header"]}>
        <h2 onClick={() => navigate("/")}>JULIIAZHIVAEVA</h2>
        <button onClick={() => navigate("/")}>
          <svg
            width="28"
            height="28"
            viewBox="0 0 28 28"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fill-rule="evenodd"
              clip-rule="evenodd"
              d="M0.43868 0.43868C0.71993 0.157779 1.10118 0 1.49868 0C1.89618 0 2.27743 0.157779 2.55868 0.43868L26.5587 24.4387C26.7061 24.576 26.8243 24.7416 26.9062 24.9256C26.9882 25.1096 27.0323 25.3082 27.0359 25.5096C27.0394 25.711 27.0024 25.9111 26.9269 26.0979C26.8515 26.2847 26.7392 26.4543 26.5968 26.5968C26.4543 26.7392 26.2847 26.8515 26.0979 26.9269C25.9111 27.0024 25.711 27.0394 25.5096 27.0359C25.3082 27.0323 25.1096 26.9882 24.9256 26.9062C24.7416 26.8243 24.576 26.7061 24.4387 26.5587L0.43868 2.55868C0.157779 2.27743 0 1.89618 0 1.49868C0 1.10118 0.157779 0.71993 0.43868 0.43868Z"
              fill="#FBE0F3"
            />
            <path
              fill-rule="evenodd"
              clip-rule="evenodd"
              d="M26.5587 0.43868C26.8396 0.71993 26.9974 1.10118 26.9974 1.49868C26.9974 1.89618 26.8396 2.27743 26.5587 2.55868L2.55872 26.5587C2.27437 26.8236 1.89827 26.9679 1.50967 26.961C1.12107 26.9542 0.750299 26.7968 0.475473 26.5219C0.200646 26.2471 0.0432229 25.8763 0.0363665 25.4877C0.0295101 25.0991 0.173756 24.723 0.438716 24.4387L24.4387 0.43868C24.72 0.157779 25.1012 0 25.4987 0C25.8962 0 26.2775 0.157779 26.5587 0.43868Z"
              fill="#FBE0F3"
            />
          </svg>
        </button>
      </div>
      <div className={styles["form_wrapper"]}>
        <h2 className={styles.tittle}>Новый пароль</h2>
        <form
          className={styles.form}
          onSubmit={handleSubmit(onSubmit)}
          noValidate
        >
          <div className={styles["input_container"]}>
            <div className={styles["input_wrapper"]}>
              <input
                type={showPassword ? "text" : "password"}
                placeholder="Новый пароль"
                {...register("password", {
                  minLength: {
                    value: 6,
                    message: "Пароль должен состоять минимум из 6 символов",
                  },
                  pattern: {
                    value: /^[A-Za-z0-9]+$/,
                    message:
                      "Пароль должен содержать только латинские буквы и цифры",
                  },
                })}
                className={`${styles.input} ${errors.password ? styles.error_border : ""}`}
              />
              <button
                onClick={() => setShowPassword(!showPassword)}
                type="button"
                className={styles["toggle"]}
              >
                <img src="/icons/eye.svg" alt="" />
              </button>
            </div>
            <p
              className={`${styles.error} ${hasPasswordError ? styles.visible : ""}`}
            >
              {passwordErrorText}
            </p>
          </div>

          <div className={styles["input_container"]}>
            <div className={styles["input_wrapper"]}>
              <input
                type={showPassword2 ? "text" : "password"}
                placeholder="Повторите пароль"
                {...register("confirm", {
                  validate: (value) => {
                    if (!passwordValue) {
                      return "Придумайте пароль!";
                    }
                    if (!value) {
                      return "Повторите пароль!";
                    }
                    if (value !== passwordValue) {
                      return "Пароли не совпадают!";
                    }
                    return true;
                  },
                  pattern: {
                    value: /^[A-Za-z0-9]+$/,
                    message:
                      "Пароль должен содержать только латинские буквы и цифры",
                  },
                })}
                className={`${styles.input} ${errors.confirm ? styles.error_border : ""}`}
              />
              <button
                onClick={() => setShowPassword2(!showPassword2)}
                type="button"
                className={styles["toggle"]}
              >
                <img src="/icons/eye.svg" alt="" />
              </button>
            </div>
            <p
              className={`${styles.error} ${hasConfirmError ? styles.visible : ""}`}
            >
              {confirmErrorText}
            </p>
          </div>

          <div className={styles["btn_wrapper"]}>
            <button type="submit" className={styles.btn}>
              ПРОДОЛЖИТЬ
              <svg
                width="7"
                height="7"
                viewBox="0 0 7 7"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M6.51037 0.500372C6.51037 0.22423 6.28651 0.000372279 6.01037 0.000372153L1.51037 0.000372553C1.23423 0.000372385 1.01037 0.22423 1.01037 0.500372C1.01037 0.776515 1.23423 1.00037 1.51037 1.00037L5.51037 1.00037L5.51037 5.00037C5.51037 5.27651 5.73423 5.50037 6.01037 5.50037C6.28651 5.50037 6.51037 5.27651 6.51037 5.00037L6.51037 0.500372ZM0.353516 6.15723L0.707069 6.51078L6.36392 0.853926L6.01037 0.500372L5.65682 0.146819L-3.77595e-05 5.80367L0.353516 6.15723Z"
                  fill="#FBE0F3"
                />
              </svg>
            </button>
            <p
              className={`${styles.error} ${hasServerError ? styles.visible : ""}`}
            >
              {serverErrorText}
            </p>
          </div>
        </form>
      </div>
    </div>
  );
}
